.banner-c {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
}

.c-logo-ltrs {
  font-size: $x2lrg;
  color: var(--blue);
  margin: .2em;
  // margin-top: 0;
  font-family: 'Rubik', sans-serif;
  display: none;
  @include bp($bp-15) {
    display: block;
  }
}

.c-logo-subltrs {
  font-size: $x2lrg;
  color: white;
  font-weight: 700;
  margin: 0;
  font-family: 'Rubik', sans-serif;
  font-style: italic;
  display: none;
  @include bp($bp-15) {
    display: block;
  }
}


